import React, { useState, useCallback, useMemo, useRef } from 'react';
import { LineChart, Line, BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer, PieChart, Pie, Cell, AreaChart, Area, ScatterChart, Scatter } from 'recharts';
import { Upload, Download, FileText, BarChart3, Settings, AlertCircle, CheckCircle } from 'lucide-react';
import * as Papa from 'papaparse';
import DataTable from './DataTable';

const CHART_TYPES = [
  { id: 'line', name: 'Line Chart', icon: '' },
  { id: 'bar', name: 'Bar Chart', icon: '' },
  { id: 'horizontalBar', name: 'Horizontal Bar Chart', icon: '' },
  { id: 'pie', name: 'Pie Chart', icon: '' },
  { id: 'donut', name: 'Donut Chart', icon: '' },
  { id: 'scatter', name: 'Scatter Plot', icon: '' },
  { id: 'area', name: 'Area Chart', icon: '' }
];

const COLOR_SCHEMES = [
  { name: 'Default', colors: ['#8884d8', '#82ca9d', '#ffc658', '#ff7300', '#00ff00', '#0088fe', '#ff8042'] },
  { name: 'Ocean', colors: ['#0077be', '#00a8cc', '#40e0d0', '#48cae4', '#90e0ef', '#ade8f4', '#caf0f8'] },
  { name: 'Sunset', colors: ['#ff6b35', '#f7931e', '#ffd23f', '#06ffa5', '#4ecdc4', '#45b7d1', '#96ceb4'] },
  { name: 'Forest', colors: ['#2d5016', '#3e6b1f', '#4f7942', '#87a96b', '#c5d5a4', '#e8f5e8', '#f0f8f0'] },
  { name: 'Berry', colors: ['#8e44ad', '#9b59b6', '#e74c3c', '#e67e22', '#f39c12', '#f1c40f', '#27ae60'] }
];

const DataVisualizationDashboard = () => {
  const [data, setData] = useState([]);
  const [columns, setColumns] = useState([]);
  const [fileName, setFileName] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  
  // Chart configuration state
  const [chartType, setChartType] = useState('bar');
  const [xAxis, setXAxis] = useState('');
  const [yAxis, setYAxis] = useState('');
  const [groupBy, setGroupBy] = useState('');
  const [chartTitle, setChartTitle] = useState('');
  const [xAxisLabel, setXAxisLabel] = useState('');
  const [yAxisLabel, setYAxisLabel] = useState('');
  const [colorScheme, setColorScheme] = useState('Default');
  
  const fileInputRef = useRef(null);
  const chartRef = useRef(null);

  // File upload handler
  const handleFileUpload = useCallback((event) => {
    const file = event.target.files?.[0];
    if (!file) return;

    // Validate file size (10MB limit)
    if (file.size > 10 * 1024 * 1024) {
      setError('File size exceeds 10MB limit');
      return;
    }

    // Validate file type
    if (!file.name.toLowerCase().endsWith('.csv')) {
      setError('Please upload a CSV file');
      return;
    }

    setLoading(true);
    setError('');
    setSuccess('');
    setFileName(file.name);

    Papa.parse(file, {
      header: true,
      skipEmptyLines: true,
      dynamicTyping: true,
      delimitersToGuess: [',', '\t', '|', ';'],
      complete: (results) => {
        if (results.errors.length > 0) {
          setError(`CSV parsing error: ${results.errors[0].message}`);
          setLoading(false);
          return;
        }

        if (results.data.length === 0) {
          setError('CSV file is empty or contains no valid data');
          setLoading(false);
          return;
        }

        const parsedData = results.data.filter(row => 
          Object.values(row).some(value => value !== null && value !== undefined && value !== '')
        );

        if (parsedData.length === 0) {
          setError('No valid data rows found in CSV');
          setLoading(false);
          return;
        }

        const columnNames = Object.keys(parsedData[0]);
        setData(parsedData.slice(0, 10000)); // Limit to 10k rows for performance
        setColumns(columnNames);
        setSuccess(`Successfully loaded ${parsedData.length} rows with ${columnNames.length} columns`);
        
        // Auto-select first columns if available
        if (columnNames.length > 0) {
          setXAxis(columnNames[0]);
          const numericColumns = columnNames.filter(col => 
            typeof parsedData[0][col] === 'number'
          );
          if (numericColumns.length > 0) {
            setYAxis(numericColumns[0]);
          }
        }
        
        setLoading(false);
      },
      error: (error) => {
        setError(`Failed to parse CSV: ${error.message}`);
        setLoading(false);
      }
    });
  }, []);

  // Get numeric columns for Y-axis selection
  const numericColumns = useMemo(() => {
    if (data.length === 0) return [];
    return columns.filter(col => typeof data[0][col] === 'number');
  }, [data, columns]);

  // Get categorical columns for grouping
  const categoricalColumns = useMemo(() => {
    if (data.length === 0) return [];
    return columns.filter(col => typeof data[0][col] === 'string' || typeof data[0][col] === 'boolean');
  }, [data, columns]);

  // Process data for charting
  const chartData = useMemo(() => {
    if (!data.length || !xAxis || !yAxis) return [];

    let processedData = data;

    // Group data if groupBy is selected
    if (groupBy && groupBy !== '') {
      const grouped = {};
      processedData.forEach(row => {
        const key = `${row[xAxis]}_${row[groupBy]}`;
        if (!grouped[key]) {
          grouped[key] = {
            [xAxis]: row[xAxis],
            [groupBy]: row[groupBy],
            [yAxis]: 0,
            count: 0
          };
        }
        grouped[key][yAxis] += Number(row[yAxis]) || 0;
        grouped[key].count += 1;
      });
      processedData = Object.values(grouped).map(item => ({
        ...item,
        [yAxis]: item[yAxis] / item.count // Average
      }));
    }

    // For pie/donut charts, aggregate by x-axis
    if (chartType === 'pie' || chartType === 'donut') {
      const aggregated = {};
      processedData.forEach(row => {
        const key = row[xAxis];
        if (!aggregated[key]) {
          aggregated[key] = { name: key, value: 0 };
        }
        aggregated[key].value += Number(row[yAxis]) || 0;
      });
      
      // Limit to top 10 categories for readability
      return Object.values(aggregated)
        .sort((a, b) => b.value - a.value)
        .slice(0, 10);
    }

    return processedData.slice(0, 1000); // Limit for performance
  }, [data, xAxis, yAxis, groupBy, chartType]);

  // Export chart as image
  const exportChart = useCallback((format) => {
    if (!chartRef.current) return;

    try {
      const canvas = document.createElement('canvas');
      const ctx = canvas.getContext('2d');
      const chartElement = chartRef.current.querySelector('.recharts-wrapper');
      
      if (chartElement) {
        const svgElement = chartElement.querySelector('svg');
        if (svgElement) {
          const svgData = new XMLSerializer().serializeToString(svgElement);
          const img = new Image();
          const svgBlob = new Blob([svgData], { type: 'image/svg+xml;charset=utf-8' });
          const url = URL.createObjectURL(svgBlob);
          
          img.onload = () => {
            canvas.width = img.width;
            canvas.height = img.height;
            ctx.drawImage(img, 0, 0);
            
            const link = document.createElement('a');
            link.download = `chart-${Date.now()}.${format}`;
            
            if (format === 'svg') {
              link.href = url;
            } else {
              link.href = canvas.toDataURL(`image/${format}`);
            }
            
            link.click();
            URL.revokeObjectURL(url);
          };
          
          img.src = url;
        }
      }
    } catch (error) {
      setError(`Failed to export chart: ${error.message}`);
    }
  }, []);

  // Export data as CSV
  const exportData = useCallback(() => {
    if (chartData.length === 0) return;

    const csv = Papa.unparse(chartData);
    const blob = new Blob([csv], { type: 'text/csv;charset=utf-8;' });
    const link = document.createElement('a');
    link.href = URL.createObjectURL(blob);
    link.download = `filtered-data-${Date.now()}.csv`;
    link.click();
  }, [chartData]);

  // Save chart configuration
  const saveConfiguration = useCallback(() => {
    const config = {
      chartType,
      xAxis,
      yAxis,
      groupBy,
      chartTitle,
      xAxisLabel,
      yAxisLabel,
      colorScheme,
      timestamp: new Date().toISOString()
    };

    const blob = new Blob([JSON.stringify(config, null, 2)], { type: 'application/json' });
    const link = document.createElement('a');
    link.href = URL.createObjectURL(blob);
    link.download = `chart-config-${Date.now()}.json`;
    link.click();
  }, [chartType, xAxis, yAxis, groupBy, chartTitle, xAxisLabel, yAxisLabel, colorScheme]);

  // Render chart based on type
  const renderChart = () => {
    if (!chartData.length) return null;

    const colors = COLOR_SCHEMES.find(scheme => scheme.name === colorScheme)?.colors || COLOR_SCHEMES[0].colors;

    const commonProps = {
      width: '100%',
      height: 400,
      data: chartData,
      margin: { top: 20, right: 30, left: 20, bottom: 60 }
    };

    switch (chartType) {
      case 'line':
        return (
          <ResponsiveContainer {...commonProps}>
            <LineChart data={chartData}>
              <CartesianGrid strokeDasharray="3 3" />
              <XAxis 
                dataKey={xAxis} 
                label={{ value: xAxisLabel || xAxis, position: 'insideBottom', offset: -10 }}
                angle={-45}
                textAnchor="end"
                height={80}
              />
              <YAxis label={{ value: yAxisLabel || yAxis, angle: -90, position: 'insideLeft' }} />
              <Tooltip />
              <Legend />
              <Line 
                type="monotone" 
                dataKey={yAxis} 
                stroke={colors[0]} 
                strokeWidth={2}
                dot={{ fill: colors[0] }}
              />
            </LineChart>
          </ResponsiveContainer>
        );

      case 'bar':
        return (
          <ResponsiveContainer {...commonProps}>
            <BarChart data={chartData}>
              <CartesianGrid strokeDasharray="3 3" />
              <XAxis 
                dataKey={xAxis} 
                label={{ value: xAxisLabel || xAxis, position: 'insideBottom', offset: -10 }}
                angle={-45}
                textAnchor="end"
                height={80}
              />
              <YAxis label={{ value: yAxisLabel || yAxis, angle: -90, position: 'insideLeft' }} />
              <Tooltip />
              <Legend />
              <Bar dataKey={yAxis} fill={colors[0]} />
            </BarChart>
          </ResponsiveContainer>
        );

      case 'area':
        return (
          <ResponsiveContainer {...commonProps}>
            <AreaChart data={chartData}>
              <CartesianGrid strokeDasharray="3 3" />
              <XAxis 
                dataKey={xAxis} 
                label={{ value: xAxisLabel || xAxis, position: 'insideBottom', offset: -10 }}
                angle={-45}
                textAnchor="end"
                height={80}
              />
              <YAxis label={{ value: yAxisLabel || yAxis, angle: -90, position: 'insideLeft' }} />
              <Tooltip />
              <Legend />
              <Area 
                type="monotone" 
                dataKey={yAxis} 
                stroke={colors[0]} 
                fill={colors[0]}
                fillOpacity={0.6}
              />
            </AreaChart>
          </ResponsiveContainer>
        );

      case 'scatter':
        return (
          <ResponsiveContainer {...commonProps}>
            <ScatterChart data={chartData}>
              <CartesianGrid strokeDasharray="3 3" />
              <XAxis 
                dataKey={xAxis} 
                label={{ value: xAxisLabel || xAxis, position: 'insideBottom', offset: -10 }}
              />
              <YAxis 
                dataKey={yAxis}
                label={{ value: yAxisLabel || yAxis, angle: -90, position: 'insideLeft' }} 
              />
              <Tooltip cursor={{ strokeDasharray: '3 3' }} />
              <Legend />
              <Scatter dataKey={yAxis} fill={colors[0]} />
            </ScatterChart>
          </ResponsiveContainer>
        );

      case 'pie':
      case 'donut':
        return (
          <ResponsiveContainer width="100%" height={400}>
            <PieChart>
              <Pie
                data={chartData}
                cx="50%"
                cy="50%"
                outerRadius={chartType === 'donut' ? 120 : 150}
                innerRadius={chartType === 'donut' ? 60 : 0}
                paddingAngle={2}
                dataKey="value"
                label={({ name, percent }) => `${name}: ${(percent * 100).toFixed(1)}%`}
              >
                {chartData.map((entry, index) => (
                  <Cell key={`cell-${index}`} fill={colors[index % colors.length]} />
                ))}
              </Pie>
              <Tooltip />
              <Legend />
            </PieChart>
          </ResponsiveContainer>
        );

      default:
        return null;
    }
  };

  return (
    <div className="min-h-screen bg-gray-50 p-4">
      <div className="max-w-7xl mx-auto">
        {/* Header */}
        <div
          className="sticky top-0 z-30 bg-gradient-to-r from-blue-600 via-blue-400 to-blue-300 rounded-lg shadow-sm p-6 mb-6"
          style={{ backdropFilter: 'blur(6px)' }}
        >
          <div className="flex items-center gap-3 mb-4">
            <BarChart3 className="h-8 w-8 text-white" />
            <h1 className="text-3xl font-bold text-white drop-shadow">Data Visualization Dashboard</h1>
          </div>
          <p className="text-blue-50">Upload CSV files and create interactive charts with customizable parameters</p>
        </div>

        {/* File Upload Section */}
        <div className="bg-white rounded-lg shadow-sm p-6 mb-6">
          <h2 className="text-xl font-semibold text-gray-900 mb-4 flex items-center gap-2">
            <Upload className="h-5 w-5" />
            File Upload
          </h2>
          
          <div className="border-2 border-dashed border-gray-300 rounded-lg p-8 text-center">
            <input
              ref={fileInputRef}
              type="file"
              accept=".csv"
              onChange={handleFileUpload}
              className="hidden"
            />
            
            {loading ? (
              <div className="flex items-center justify-center gap-2">
                <div className="animate-spin rounded-full h-6 w-6 border-b-2 border-blue-600"></div>
                <span className="text-gray-600">Processing file...</span>
              </div>
            ) : (
              <div>
                <FileText className="h-12 w-12 text-gray-400 mx-auto mb-4" />
                <button
                  onClick={() => fileInputRef.current?.click()}
                  className="bg-blue-600 text-white px-6 py-2 rounded-lg hover:bg-blue-700 transition-colors"
                >
                  Choose CSV File
                </button>
                <p className="text-gray-500 mt-2">Maximum file size: 10MB</p>
                {fileName && (
                  <p className="text-sm text-gray-700 mt-2">Selected: {fileName}</p>
                )}
              </div>
            )}
          </div>

          {/* Status Messages */}
          {error && (
            <div className="mt-4 p-4 bg-red-50 border border-red-200 rounded-lg flex items-center gap-2">
              <AlertCircle className="h-5 w-5 text-red-500" />
              <span className="text-red-700">{error}</span>
            </div>
          )}

          {success && (
            <div className="mt-4 p-4 bg-green-50 border border-green-200 rounded-lg flex items-center gap-2">
              <CheckCircle className="h-5 w-5 text-green-500" />
              <span className="text-green-700">{success}</span>
            </div>
          )}

          {/* Data Preview */}
          {data.length > 0 && (
            <div className="mt-6">
              <h3 className="text-lg font-medium text-gray-900 mb-3">Data Preview (First 5 rows)</h3>
              <div className="overflow-x-auto">
                <table className="min-w-full border border-gray-200 rounded-lg">
                  <thead className="bg-gray-50">
                    <tr>
                      {columns.map((col) => (
                        <th key={col} className="px-4 py-2 text-left text-sm font-medium text-gray-700 border-b">
                          {col}
                        </th>
                      ))}
                    </tr>
                  </thead>
                  <tbody>
                    {data.slice(0, 5).map((row, idx) => (
                      <tr key={idx} className="hover:bg-gray-50">
                        {columns.map((col) => (
                          <td key={col} className="px-4 py-2 text-sm text-gray-900 border-b">
                            {String(row[col] ?? '')}
                          </td>
                        ))}
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            </div>
          )}
        </div>

        {/* Chart Configuration */}
        {data.length > 0 && (
          <div className="grid grid-cols-1 lg:grid-cols-3 gap-6 mb-6">
            <div className="lg:col-span-1">
              <div className="bg-white rounded-lg shadow-sm p-6">
                <h2 className="text-xl font-semibold text-gray-900 mb-4 flex items-center gap-2">
                  <Settings className="h-5 w-5" />
                  Chart Configuration
                </h2>

                {/* Chart Type Selection */}
                <div className="mb-6">
                  <label className="block text-sm font-medium text-gray-700 mb-2">Chart Type</label>
                  <div className="grid grid-cols-2 gap-2">
                    {CHART_TYPES.map((type) => (
                      <button
                        key={type.id}
                        onClick={() => setChartType(type.id)}
                        className={`p-3 rounded-lg border-2 text-sm transition-colors ${
                          chartType === type.id
                            ? 'border-blue-500 bg-blue-50 text-blue-700'
                            : 'border-gray-200 hover:border-gray-300'
                        }`}
                      >
                        <div className="text-lg mb-1">{type.icon}</div>
                        {type.name}
                      </button>
                    ))}
                  </div>
                </div>

                {/* Axis Selection */}
                <div className="space-y-4">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">X-Axis</label>
                    <select
                      value={xAxis}
                      onChange={(e) => setXAxis(e.target.value)}
                      className="w-full p-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                    >
                      <option value="">Select column...</option>
                      {columns.map((col) => (
                        <option key={col} value={col}>{col}</option>
                      ))}
                    </select>
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">Y-Axis</label>
                    <select
                      value={yAxis}
                      onChange={(e) => setYAxis(e.target.value)}
                      className="w-full p-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                    >
                      <option value="">Select column...</option>
                      {numericColumns.map((col) => (
                        <option key={col} value={col}>{col}</option>
                      ))}
                    </select>
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">Group By (Optional)</label>
                    <select
                      value={groupBy}
                      onChange={(e) => setGroupBy(e.target.value)}
                      className="w-full p-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                    >
                      <option value="">No grouping</option>
                      {categoricalColumns.map((col) => (
                        <option key={col} value={col}>{col}</option>
                      ))}
                    </select>
                  </div>
                </div>

                {/* Styling Options */}
                <div className="mt-6 space-y-4">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">Chart Title</label>
                    <input
                      type="text"
                      value={chartTitle}
                      onChange={(e) => setChartTitle(e.target.value)}
                      placeholder="Enter chart title..."
                      className="w-full p-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                    />
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">X-Axis Label</label>
                    <input
                      type="text"
                      value={xAxisLabel}
                      onChange={(e) => setXAxisLabel(e.target.value)}
                      placeholder="X-axis label..."
                      className="w-full p-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                    />
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">Y-Axis Label</label>
                    <input
                      type="text"
                      value={yAxisLabel}
                      onChange={(e) => setYAxisLabel(e.target.value)}
                      placeholder="Y-axis label..."
                      className="w-full p-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                    />
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">Color Scheme</label>
                    <select
                      value={colorScheme}
                      onChange={(e) => setColorScheme(e.target.value)}
                      className="w-full p-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                    >
                      {COLOR_SCHEMES.map((scheme) => (
                        <option key={scheme.name} value={scheme.name}>{scheme.name}</option>
                      ))}
                    </select>
                  </div>
                </div>

                {/* Export Options */}
                <div className="mt-6 pt-6 border-t border-gray-200">
                  <h3 className="text-sm font-medium text-gray-700 mb-3">Export Options</h3>
                  <div className="space-y-2">
                    <div className="flex gap-2">
                      <button
                        onClick={() => exportChart('png')}
                        disabled={!chartData.length}
                        className="flex-1 px-3 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 disabled:bg-gray-300 disabled:cursor-not-allowed text-sm"
                      >
                        PNG
                      </button>
                      <button
                        onClick={() => exportChart('jpeg')}
                        disabled={!chartData.length}
                        className="flex-1 px-3 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 disabled:bg-gray-300 disabled:cursor-not-allowed text-sm"
                      >
                        JPEG
                      </button>
                      <button
                        onClick={() => exportChart('svg')}
                        disabled={!chartData.length}
                        className="flex-1 px-3 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 disabled:bg-gray-300 disabled:cursor-not-allowed text-sm"
                      >
                        SVG
                      </button>
                    </div>
                    <button
                      onClick={exportData}
                      disabled={!chartData.length}
                      className="w-full px-3 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700 disabled:bg-gray-300 disabled:cursor-not-allowed text-sm flex items-center justify-center gap-2"
                    >
                      <Download className="h-4 w-4" />
                      Export Data (CSV)
                    </button>
                    <button
                      onClick={saveConfiguration}
                      className="w-full px-3 py-2 bg-gray-600 text-white rounded-lg hover:bg-gray-700 text-sm"
                    >
                      Save Configuration
                    </button>
                  </div>
                </div>
              </div>
            </div>

            {/* Chart Display */}
            <div className="lg:col-span-2">
              <div className="bg-white rounded-lg shadow-sm p-6">
                <div className="flex items-center justify-between mb-4">
                  <h2 className="text-xl font-semibold text-gray-900">
                    {chartTitle || `${CHART_TYPES.find(t => t.id === chartType)?.name || 'Chart'}`}
                  </h2>
                  <span className="text-sm text-gray-500">
                    {chartData.length} data points
                  </span>
                </div>

                {xAxis && yAxis && chartData.length > 0 ? (
                  <div ref={chartRef} className="w-full">
                    {renderChart()}
                  </div>
                ) : (
                  <div className="h-96 flex items-center justify-center bg-gray-50 rounded-lg">
                    <div className="text-center">
                      <BarChart3 className="h-16 w-16 text-gray-400 mx-auto mb-4" />
                      <p className="text-gray-500">
                        {!xAxis || !yAxis 
                          ? 'Select X and Y axis columns to generate chart'
                          : 'No data available for selected configuration'
                        }
                      </p>
                    </div>
                  </div>
                )}
              </div>
            </div>
          </div>
        )}

        {/* Full Data Table */}
        {data.length > 0 && (
          <div className="mt-6">
            <h3 className="text-lg font-medium text-gray-900 mb-3">Full Data Table</h3>
            <DataTable columns={columns} data={data} />
          </div>
        )}
      </div>
      {/* Sticky Footer */}
      <footer className="sticky bottom-0 z-30 bg-gradient-to-r from-blue-300 via-blue-400 to-blue-600 text-white text-center py-3 rounded-t-lg shadow-lg mt-8">
        <span className="font-medium"> {new Date().getFullYear()} Data Visualization Dashboard</span>
      </footer>
    </div>
  );
};

export default DataVisualizationDashboard;