import React, { useMemo, useState } from 'react';
import { useReactTable, getCoreRowModel, getSortedRowModel, getFilteredRowModel, flexRender } from '@tanstack/react-table';

const DataTable = ({ columns, data }) => {
  const [globalFilter, setGlobalFilter] = useState('');
  const [sorting, setSorting] = useState([]);
  const [pageIndex, setPageIndex] = useState(0);
  const pageSize = 10;

  const table = useReactTable({
    data,
    columns: useMemo(() => columns.map(col => ({
      accessorKey: col,
      header: col,
      cell: info => String(info.getValue() ?? ''),
    })), [columns]),
    state: { sorting, globalFilter },
    onSortingChange: setSorting,
    onGlobalFilterChange: setGlobalFilter,
    getCoreRowModel: getCoreRowModel(),
    getSortedRowModel: getSortedRowModel(),
    getFilteredRowModel: getFilteredRowModel(),
    manualPagination: false,
    pageCount: Math.ceil(data.length / pageSize),
  });

  // Pagination logic
  const pagedRows = table.getRowModel().rows.slice(pageIndex * pageSize, (pageIndex + 1) * pageSize);

  return (
    <div>
      <input
        value={globalFilter}
        onChange={e => setGlobalFilter(e.target.value)}
        placeholder="Search..."
        className="mb-2 p-2 border rounded"
      />
      <table className="min-w-full border border-gray-200 rounded-lg">
        <thead>
          {table.getHeaderGroups().map(headerGroup => (
            <tr key={headerGroup.id}>
              {headerGroup.headers.map(header => (
                <th
                  key={header.id}
                  onClick={header.column.getToggleSortingHandler()}
                  className="px-4 py-2 cursor-pointer border-b"
                >
                  {flexRender(header.column.columnDef.header, header.getContext())}
                  {header.column.getIsSorted() ? (header.column.getIsSorted() === 'asc' ? ' ' : ' ') : ''}
                </th>
              ))}
            </tr>
          ))}
        </thead>
        <tbody>
          {pagedRows.map(row => (
            <tr key={row.id}>
              {row.getVisibleCells().map(cell => (
                <td key={cell.id} className="px-4 py-2 border-b">
                  {flexRender(cell.column.columnDef.cell, cell.getContext())}
                </td>
              ))}
            </tr>
          ))}
        </tbody>
      </table>
      {/* Pagination Controls */}
      <div className="flex gap-2 mt-2">
        <button
          onClick={() => setPageIndex(0)}
          disabled={pageIndex === 0}
          className="px-2 py-1 border rounded disabled:opacity-50"
        >First</button>
        <button
          onClick={() => setPageIndex(i => Math.max(i - 1, 0))}
          disabled={pageIndex === 0}
          className="px-2 py-1 border rounded disabled:opacity-50"
        >Prev</button>
        <span className="px-2 py-1">Page {pageIndex + 1} of {Math.ceil(data.length / pageSize)}</span>
        <button
          onClick={() => setPageIndex(i => Math.min(i + 1, Math.ceil(data.length / pageSize) - 1))}
          disabled={pageIndex >= Math.ceil(data.length / pageSize) - 1}
          className="px-2 py-1 border rounded disabled:opacity-50"
        >Next</button>
        <button
          onClick={() => setPageIndex(Math.ceil(data.length / pageSize) - 1)}
          disabled={pageIndex >= Math.ceil(data.length / pageSize) - 1}
          className="px-2 py-1 border rounded disabled:opacity-50"
        >Last</button>
      </div>
    </div>
  );
};

export default DataTable;